html {
  font-size: 62.5%;
}

.App {
  background-color: #fff;
  font-size: 1.5rem;
}

.bio-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-width: 80%;
  margin: auto;
  margin-top: 5px;
  flex-wrap: wrap;
  border-bottom: solid 1px #ddd;

  .img-div {
    width: 200px;
  }

  .introduction {
    max-width: 500px;
  }
}

.portfolio {
  padding: 10px 30px;
  position: absolute;
  width: 100%;

  .portfolio-title {
    text-align: center;
  }

  .portfolio-img {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    width: 50%;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
}

.carousel-indicators li {
  border: 1px solid #000;
  border-radius: 50px;
}

.carousel-inner {
  .carousel-item {
    .carousel-caption {
      color: #000;
      width: 50%;
      text-align: center;
      left: 50%;
      transform: translateX(-50%);
      h3 {
        font-size: 1.1rem;
      }
      p {
        font-size: 0.7rem;
      }
    }
  }
}

.project-panel {
  width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  .project-panel--item {
    width: 280px;
    .project-panel--img-div {
      display: block;
      height: 150px;
    }
    .project-panel--img {
      width: 85%;
    }
  }
}

.navbar {
  padding: 10px 30px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  width: 70%;
  height: auto;
  top: 50%;
  left: 50%;
  padding: 15px;
  transform: translate(-50%, -50%);
  .close-button {
    position: absolute;
    border-radius: 30px;
    top: -10px;
    right: -10px;
  }
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
